@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,600,700);
body {
  font-family: "Roboto";
  margin: 0;
  padding: 0;
  width: 100%; 
  height: 100%;
}

p{
  font-family: "Roboto";
  margin: 0;
  padding: 0;
}

.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  position: absolute;
  top: 30%;
  right: 45%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


.not-found{
  background: #FABB00;
  border-radius: 7px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  text-align: center;
  color: #1C2D4D;
  padding: 40px;
}

.large-font-size{
  font-size: 200px;
}

.list{
  text-align: left;
}
.delay-form {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 30px 5px 60px;
  background: #fff;
  box-shadow: 0px 0px 29px -9px rgba(0, 0, 0, 0.93);
  padding: 25px 10px 30px;
  align-items: center;
  justify-content: space-around;
  border-radius: 5px;
}

.input-field--select {
  flex-grow: 1;
}

.delay-form__flex-wrapper {
  display: flex;
  justify-content: space-between;
  flex-grow: 2;
  align-items: center;
}

.delay-page__wrapper {
  padding: 0 5px;
  max-width: 1250px;
  margin: 0 auto 30px;
}

.delay-page__wrapper button {
  cursor: pointer;
}

.delay-form__flex-wrapper {
  display: flex;
  justify-content: space-evenly;
}

.delay-page__title {
  text-align: center;
}

.delay-page__button {
  color: #005da8;
  border: 1px solid #005da8;
  border-radius: 3px;
  width: 290px;
  height: 43px;
  background: white;
  margin-top: 40px;
}

.delay-page__button__wrapper {
  text-align: right;
}

.delay-page__button:disabled {
  color:#b1bcc5;
  border-color: #b1bcc5;
  cursor: auto;
  cursor: initial;
}

.delay-page__button:focus {
  outline: none;
}

.input-field {
  position: relative;
  margin-bottom: 22px;
}

.delay-form__label {
  position: absolute;
  display: block;
  color: #00519e;
  font-size: 12px;
  left: 10px;
}

.delay-form__label.delay-form__label--select {
  top: 10px;
}

.station-select {
  min-width: 242px;
  height: 47px;
  font-weight: normal;
  font-size: 18px;
  display: inline-block;
}

