.location-button {
  border: 1px solid #005da8;
  border-radius: 2px;
  height: 47px;
  width: 47px;
  position: relative;
  left: 10px;
  top: 12px;
  background: center no-repeat url(data:image/png;base64,),
    rgb(255, 255, 255);
}

.delay-form__date-picker {
  border: none;
  border-bottom: 1px solid #999999;
  font-size: 18px;
  padding: 10px;
  width: 107px;
  margin-right: 20px;
}

/* Checkboxes */

.invisibleCheckbox {
  display: none;
}

.checkbox-label:hover + .popup {
  display: block;
  opacity: 1;
}

.invisibleCheckbox:checked + .checkbox-label::before {
  content: "âœ”";
  color: white;
  margin-left: 5px;
  position: absolute;
}

.checkbox-wrapper:first-child {
  margin-bottom: 0;
}

.checkbox-wrapper {
  display: inline-block;
  position: relative;
  bottom: 10px;
}

.checkbox-label {
  cursor: pointer;
  display: block;
  border-radius: 4px;
  width: 20px;
  height: 20px;
  background-color: #005da8;
  margin-right: 80px;
  border: 1px solid #999999;
  position: relative;
}

.radio-label {
  position: relative;
    left: 26px;
}


.delay-form__button {
  background: #fcc834;
  border: 1px solid #c19800;
  border-radius: 2px;
  font-weight: bold;
  font-size: 18px;
  height: 50px;
  width: 100%;
  max-width: 314px;
}

@media (min-width: 730px) {
  .delay-page__wrapper {
    padding: 0 5px;
  }

  .location-button {
    margin-right: 25px;
  }

  .delay-form {
    flex-direction: row;
  }

  .input-field {
    margin-bottom: 0;
  }
  .checkbox-wrapper {
    display: block;
    bottom: 2px;
  }
  .checkbox-wrapper:first-child {
    margin-bottom: 7px;
  }
  .delay-form__button {
    width: 192px;
  }

  .delay-form__date-picker {
    width: 147px;
  }
}

.delay-table {
  width: 100%;
  border-spacing: 0px;
  display: none;
}

.delay-table--mobile {
  display: table;
}

.delay-table th {
  padding: 5px 5px;
  text-align: left;
}

.delay-table__header {
  text-transform: uppercase;
  color: #999999;
}

.delay-table__header.delay-table__header--mobile {
  font-size: 12px;
  line-height: 21px;
}

.over-column {
  min-width: 100px;
}

/* LOADER */

.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  position: absolute;
  top: 30%;
  right: 45%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



@media (min-width: 730px) {
  .delay-table {
    display: table;
  }

  .delay-table--mobile {
    display: none;
  }

  .delay-table__row td:first-child {
    padding-left: 20px;
  }
}
.row-delay {
  width: 100%;
  background: #2f4858;
  display: flex;
  border-bottom: 2px solid #c3ccd2;
}
.rowItem-delay {
  display: flex;
  padding: 10px;
  position: relative;
  align-self: center;
}

.row-item-text-delay {
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 34px;
  color: #ffffff;
  max-height: 100px;
  white-space: pre-line;
}

.time-with-delay {
  color: #e2003b;
  margin-left: 10px;
}

.delay-table__row__arrival-time {
  font-weight: bold;
}

.delay-table__row .live-line-wrapper-row {
  margin-bottom: 14px;
}

.last-column {
  display: flex;
  justify-content: space-evenly;
  align-items: center;

}

.circle-toggle {
  width: 23px;
  height: 23px;
  border-radius: 100%;
  background: center no-repeat url(data:image/png;base64,), #005da8;
  border: none;
  outline: none;
}

.delay-table__row--opened .circle-toggle {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.train-icon-delay {
  margin-right: 10px;
  height: 25px;
}

.delay-table__row td {
  font-size: 14px;
  border-collapse: collapse;
  border-bottom: 1px solid #e3e3e3;
  padding: 15px 5px;
  border-spacing: 0px;
}

.delay-table--mobile .delay-table__row td {
  border-bottom: 1px solid #999999;
}

.delay-table__row td:first-child {
  padding-left: 5px;
}

.delay-table__row__destination {
  font-weight: bold;
  color: #00519e;
}

.delay-table__row__delay--positive {
  font-weight: bold;
  color: #32ab1e;
}

.delay-table__row__delay--negative svg {
  margin-left: 6px;
}

.delay-table__row__delay--positive svg {
  display: none;
}

.delay-table__row__delay--negative {
  font-weight: bold;
  color: #e2003b;
}

.delay-table__row__addition td {
  background: #e5e5e5;
  border-radius: 0 0 3px 3px;
  padding: 0 6px 20px;
}

.delay-table__row__addition h4 {
  font-weight: bold;
  font-size: 10px;
  color: #999999;
  text-transform: uppercase;
  margin-top: 20px;
}

.delay-table__row__addition p {
  font-size: 14px;
  color: #292929;
}

.delay-table__row--opened {
  background: #e5e5e5;
}

.delay-table__row--opened td:first-child {
  border-radius: 3px 0 0 0;
}

.delay-table__row--opened td:last-child {
  border-radius: 0 3px 0 0;
}
.live-line-wrapper-row {
  width: 100%;
	position: absolute; 
	left: 0;
	bottom: -12px;
  white-space: nowrap;
}
.live-line-wrapper-row .live-line-row {
  padding-right: 200px;
  word-spacing: 50px;
  font-weight: 300;
}


